<template>
  <div class="mt-5">
    <div class="card m-auto p-5 border-success">
      <p class="text-center fw-bold h4 text-wrap">User Profile</p>
      <img
        :src="require('../assets/user.png')"
        alt="user_image"
        class="m-auto userimg border border-primary p-2"
      />
      <p class="text-center mt-3 fw-bold h3">{{ user.role }}</p>
      <hr class="border-primary" />
      <div class="user-details fw-semibold fs-5 m-auto">
        <p>Name: {{ user.name }}</p>
        <p>Username: {{ user.username }}</p>
        <p>Email: {{ user.email }}</p>
        <!-- <button
          class="btn btn-success m-auto"
          data-bs-toggle="modal"
          data-bs-target="#exampleModal"
        >
          Edit Profile
        </button> -->

        <!-- Edit profile Modal -->
        <div
          class="modal fade"
          id="exampleModal"
          tabindex="-1"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title text-center" id="exampleModalLabel">
                  Edit Profile
                </h5>
                <button
                  type="button"
                  class="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <!-- modal body -->
              <div class="modal-body">
                <div class="card-body">
                  <form @submit.prevent="">
                    <div class="form-group">
                      <label for="email">Email</label>
                      <input
                        type="text"
                        id="email"
                        name="email"
                        placeholder="Enter email"
                        v-model="email"
                        class="form-control"
                      />
                    </div>
                    <div class="form-group">
                      <label for="password">Password</label>
                      <input
                        type="password"
                        id="password"
                        name="password"
                        placeholder="Enter password"
                        v-model="password"
                        class="form-control"
                      />
                    </div>
                    <div class="form-group">
                      <label for="confirm_password">Confirm Password</label>
                      <input
                        type="password"
                        id="confirm_password"
                        name="confirm_password"
                        placeholder="Confirm Password"
                        v-model="confirm_password"
                        class="form-control"
                      />
                    </div>

                    <button class="btn btn-success mt-2">Save Changes</button>
                  </form>
                </div>
              </div>
              <div class="modal-footer">
                <button
                  type="button"
                  class="btn btn-secondary"
                  data-bs-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "EditProfile",
  methods: {
    ...mapActions(["getProfile"]),
  },
  computed: {
    ...mapGetters(["user"]),
  },
  created() {
    this.getProfile();
  },
};
</script>

<style scoped>
.card {
  width: 50%;
}
hr {
  border: 2px solid;
}
.userimg {
  border-radius: 50%;
  height: 10em;
}

label {
  font-weight: 600;
  margin: 0.5em 0 0.5em 0;
}

@media only screen and (max-width: 600px) {
  .card {
    width: auto;
  }
}
</style>